@import url(https://fonts.googleapis.com/css2?family=Rajdhani:wght@400;700&display=swap);
html {
  height: 100%;
  font-size: 62.5%;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  font-family: 'Rajdhani', sans-serif;;    
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  font-size: 1.6rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home {
  width: 100%;
  position: relative;
  height: 93vh;
  background: var(--primary-color);
  background: linear-gradient(to right, rgba(6, 2, 15, 0.9), rgba(6, 2, 15, .9)), url(/static/media/7038048-purple-dark-smoke.4f43aba5.jpg);
  background-size: cover;
  background-position: center;
}

.image-container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}


.home-image {
  width: 300px;
  height: 250px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-filter: saturate(0.5) drop-shadow(-1px -1px 2rem black);
  filter: saturate(0.5) drop-shadow(-1px -1px 2rem black);
  border-radius: 50%;
}

.home-text-container {
  padding: 0 15px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  color: var(--white);
  font-size: 7rem;
  width: 100%;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}

.custom-row {
  margin-left: 0;
  margin-right: 0;
  margin-bottom: -20px;
}

.custom-row:first-child {
  color: var(--secondary-color);
  font-weight: bold;
  font-size: 8rem;
}

.custom-row:last-child {
  margin-bottom: 0;
  margin-top: 20px;
}

.home-iams {
  margin-right: 20px;
}

.animationtexts {
  color: var(--secondary-color);
  font-weight: 700;
}

.home-button {
  -webkit-clip-path: ellipse(10%);
  clip-path: ellipse(10%);
  text-decoration: none;
  color: var(--white);
  position: relative;
  /* border-radius: 3rem; */
  /* border-width: 0; */
  border: none;
  background-color: rgba(61, 55, 76, 0.2);
  font-size: 2rem;
  padding: 1.5rem;
  margin-top: 3rem;
  /* -webkit-animation-name: moveInBottom;
  animation-name: moveInBottom;
  -webkit-animation-duration: .8s;
  animation-duration: .8s; */
  transition: all .2s;
  /* border-color: var(--secondary-color); */
}

.home-button span {
  position: absolute;
  display: block;
  background: var(--secondary-color);
}

.home-button span:nth-child(1) {
  left: 0;
  bottom: 0;
  width: 1px;
  height: 100%;
  -webkit-transform: scaleY(0);
  transform: scaleY(0);
  -webkit-transform-origin: top;
  transform-origin: top;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
}

.home-button:hover span:nth-child(1) {
  -webkit-transform: scaleY(1);
  transform: scaleY(1);
  -webkit-transform-origin: bottom;
  transform-origin: bottom;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
}

.home-button span:nth-child(2) {
  left: 0;
  top: 0;
  width: 100%;
  height: 1px;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: right;
  transform-origin: right;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  transition-delay: 0.25s;
}

.home-button:hover span:nth-child(2) {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  -webkit-transform-origin: left;
  transform-origin: left;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  transition-delay: 0.25s;
}

.home-button span:nth-child(3) {
  right: 0;
  bottom: 0;
  width: 1px;
  height: 100%;
  -webkit-transform: scaleY(0);
  transform: scaleY(0);
  -webkit-transform-origin: bottom;
  transform-origin: bottom;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  transition-delay: 0.5s;

}

.home-button:hover span:nth-child(3) {
  -webkit-transform: scaleY(1);
  transform: scaleY(1);
  -webkit-transform-origin: top;
  transform-origin: top;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  transition-delay: 0.5s;
}

.home-button span:nth-child(4) {
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: left;
  transform-origin: left;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  transition-delay: 0.75s;

}

.home-button:hover span:nth-child(4) {
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  -webkit-transform-origin: right;
  transform-origin: right;
  transition: -webkit-transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
  transition-delay: 0.75s;
}

.home-button:hover {
  text-decoration: none;
  background: var(--secondary-color);
  transition: 0.5s;
  transition-delay: 1s;
  font-weight: bold;
  color: var(--black);
  background-color: var(--secondary-color);
  border-color: var(--secondary-color);
  box-shadow: 0 0.6rem 2rem rgba(0, 0, 0, 0.8);
  -webkit-transform: translateY(-0.5rem);
  transform: translateY(-0.5rem);
}

.home-button:active {
  box-shadow: 0 0.6rem 2.7rem rgba(0, 0, 0, 0.8);
  -webkit-transform: translateY(-0.2rem);
  transform: translateY(-0.2rem);
}


.home-iconss {
  color: var(--white);
  width: 100%;
  height: 100%;
  font-size: 3rem;
  border-radius: 50%;
  border: 1px solid var(--white);
}

.icons {
  list-style: none;
  margin-top: 20px;

}

.icons li {
  font-size: 1.6rem;
  cursor: pointer;
  overflow: hidden;
  position: relative;
  margin-right: 20px;
  height: 60px;
  width: 60px;
  background: var(--primary-color);
  border-radius: 50%;
  text-align: center;
  box-shadow: inset -1px -1px 2px var(--primary-color), inset 1px 1px 2px var(--secondary-color);
}

.icons li a .icon1 {
  color: var(--white);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  font-size: 2.5rem;
}

.icons li::before {
  content: '';
  position: absolute;
  background-color: var(--secondary-color);
  height: 200%;
  width: 150%;
  left: -50%;
  bottom: 150%;
  -webkit-transform: rotate(50deg);
  transform: rotate(50deg);
}

.icons li:hover::before {
  -webkit-animation: slide 1s linear;
  animation: slide 1s linear;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
}

.icons li:hover .facebook {
  color: var(--black)
}

.icons li:hover .linkedin {
  color: var(--black)
}

.icons li:hover .github {
  color: var(--black)
}

@media only screen and (max-width:990px) {
  .home-text-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    font-size: 5rem;
    top: 0%;
    -webkit-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
  }

  .custom-row:first-child {
    display: none;
  }

  .custom-row:last-child {
    margin-left: -20px;
  }

  .icons li {
    width: 50px;
    height: 50px;
  }

}

@media only screen and (max-width:490px) {
  .home-text-container {
    font-size: 3.3rem;
  }

  .home-iams {
    margin-right: 10px;
  }

  .home-button {
    margin-top: 2rem;
  }

  .icons li {
    width: 40px;
    height: 40px;
  }

  .icons li a .icon1 {
    font-size: 2rem;
  }

}

@-webkit-keyframes slide {
  0% {}

  25% {
    bottom: 0%;
  }

  75% {
    bottom: 80%;
    -webkit-transform: rotate(60deg);
    transform: rotate(60deg);
  }

  100% {
    bottom: -50%;
  }
}

@keyframes slide {
  0% {}

  25% {
    bottom: 0%;
  }

  75% {
    bottom: 80%;
    -webkit-transform: rotate(60deg);
    transform: rotate(60deg);
  }

  100% {
    bottom: -50%;
  }
}

