  .work-main__1,
  .work-main__2 {
    display: none;
  }

  .work-main__3 {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }



}
.form-control {
  background-color: var(--primary-color-medium);
  border-radius: 1rem;
  border-color: var(--primary-color-light);
